export const buttonVariants = {
	base: 'inline-flex items-center justify-center whitespace-nowrap rounded-md text-sm font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50',
	variants: {
		default: 'bg-primary text-primary-foreground hover:bg-primary/90',
		destructive: 'bg-destructive text-destructive-foreground hover:bg-destructive/90',
		outline: 'border border-input bg-background hover:bg-accent hover:text-accent-foreground',
		secondary: 'bg-secondary text-secondary-foreground hover:bg-secondary/80',
		ghost: 'hover:bg-accent hover:text-accent-foreground',
		link: 'text-primary underline-offset-4 hover:underline'
	},
	sizes: {
		default: 'h-10 px-4 py-2',
		sm: 'h-9 rounded-md px-3',
		lg: 'h-11 rounded-md px-8',
		icon: 'h-10 w-10'
	}
};

export function getButtonVariants({ variant = 'default', size = 'default', className = '' }) {
	const variantClasses = buttonVariants.variants[variant] || '';
	const sizeClasses = buttonVariants.sizes[size] || '';

	return `${buttonVariants.base} ${variantClasses} ${sizeClasses} ${className}`.trim();
}

export class Button extends HTMLElement {
	connectedCallback() {
		const variant = this.getAttribute('variant') || 'default';
		const size = this.getAttribute('size') || 'default';
		const userClass = this.getAttribute('class') || '';
		const href = this.getAttribute('href');
		const asChild = this.hasAttribute('asChild');

		let element;

		if (asChild && this.firstElementChild) {
			// Use the first child element if asChild is true
			element = this.firstElementChild;
			// Remove it from the custom element to avoid duplication
			this.removeChild(element);
		} else {
			// Create either a button or anchor element based on the presence of href
			element = document.createElement(href ? 'a' : 'button');
			element.innerHTML = this.innerHTML;
		}

		// Apply button variants and user classes
		const variantClasses = getButtonVariants({ variant, size, className: userClass });
		element.className = element.className
			? `${element.className} ${variantClasses}`
			: variantClasses;

		// Apply all attributes from the custom element to the element
		for (const attr of this.attributes) {
			if (attr.name !== 'variant' && attr.name !== 'size' && attr.name !== 'class' && attr.name !== 'asChild') {
				element.setAttribute(attr.name, attr.value);
			}
		}

		// If it's an anchor, ensure it has the href attribute
		if (href && !asChild) {
			element.setAttribute('href', href);
		}

		// Replace the custom element with the newly created or modified element
		this.replaceWith(element);
	}
}
